@font-face {
  font-family: "PoetsenOne";
  font-style: normal;
  font-weight: normal;
  src: local("PoetsenOne"), url("./fonts/PoetsenOne-Regular.woff") format("woff");
}
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  font-family: "PoetsenOne", sans-serif;
}

body,
.page {
  min-height: 100vh;
  background-image: url(./pictures/login.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.form-login {
  min-width: 50vw;
  background: rgba(224, 106, 90, 0.71);
  padding: 4%;
  width: 527px;
  height: 500px;
  margin: 4%;
}
.form-login .btn {
  color: #EA523D;
}

.form-register {
  background: rgba(224, 106, 90, 0.71);
  padding: 3%;
  margin: 100px 4%;
}

.navbar-brand {
  color: #EA523D;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: PoetsenOne;
  font-size: 76px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}